import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'rxAssociatedRecordFieldNamePipe'
})
export class RxAssociatedRecordFieldNamePipe implements PipeTransform {
  transform(value: string, associatedRecordFieldNames: { [key: string]: string }): string {
    return associatedRecordFieldNames?.[value] || value;
  }
}
